import React, { useState, useContext } from "react";

const RandomNumber = () => {
  const [number, setNumber] = useState(0);

  const fetchNumber = async () => {
    const url =
      "https://wvv2lnqscf.execute-api.eu-west-2.amazonaws.com/dev/random-number?min=1&max=100";

    const data = await fetch(url, { method: "GET", mode: "cors" })
      .then((response) => response.json())
      .catch((err) => {
        console.error(err);
      });

    setNumber(data);
  };

  return (
    <div>
      <div>Random number: {number}</div>
      <button onClick={fetchNumber}>Fetch new number</button>
    </div>
  );
};

export default RandomNumber;
